<template>
    <div class="form-group d-flex align-items-center">
        <label class="control-label">是否选中</label>
        <div class="form-group-w">
           <label class="form-group-labe" 
                :class="{'selected':isChoose}" 
                @click="togglePay"
            >
                <span class="ide-checkbox"></span>
            </label>
        </div>
    </div>
</template>
<script>
export default {
    name: 'FormCheckbox',
    data(){
        return {
            isChoose: false,
        }
    },
    methods: {
        togglePay(){
            this.isChoose = !this.isChoose;
        }
    }
}
</script>