<template>
    <div class="ide-left-sidebar">
        <LeftsidebarLayout></LeftsidebarLayout>
        <LeftsidebarAssembly :widgets="formData"></LeftsidebarAssembly>
    </div>
</template>
<script>
import LeftsidebarLayout from '@/components/layout/LeftsidebarLayout'
import LeftsidebarAssembly from '@/components/layout/LeftsidebarAssembly'
import { groupTypes ,widgetItems } from '@/config/stencilset'

let formData = {}

widgetItems.forEach((widget)=>{
    if(!formData[groupTypes[widget.group]]){
        formData[groupTypes[widget.group]] = []
    }
    formData[groupTypes[widget.group]].push(widget)
})


export default {
    name: 'LeftSidebar',
    components:{
        LeftsidebarLayout,
        LeftsidebarAssembly,
    },
    data(){
        return {
            formData: formData
        }
    }
}
</script>