/* eslint-disable */ 
import UTIL from '@/utils/util'

import { propertyPackages } from '@/config/stencilset.js'

import Property from './property'

class Widget {
    constructor(option) {
		this.type = option.type
        this.view = option.view
        this.name = option.name
		this.propertys = []
		this.uuid = UTIL.guid()
		this.add(option)
    }
    
    add(option){
		let _this = this
        propertyPackages.forEach((property)=>{
            option.propertyPackages.forEach((propertyName)=>{
                if(propertyName == property.name){
                    _this.propertys.push(new Property(property))
                }
            });
        });
 	}
}

export default Widget;