<template>
    <div class="form-group d-flex align-items-center">
        <label class="control-label">按钮设置</label>
        <div class="form-group-w">
            <div class="input-group">
                <button type="button" @click="openMask" class="btn btn-primary" role="button">点击设置</button>
            </div>
        </div>
        <JCDialog 
            v-model="sendVal" 
            @cancel="clickCancel()" 
            @danger="clickDanger()" 
            @confirm="clickConfirm()"
            type="confirm" :title="title" ><AddBtn slot="D-option"></AddBtn></JCDialog>
    </div>
</template>
<script>
import JCDialog from './JCDialog'
import AddBtn from './AddBtn'
export default {
    name: 'FormButton',
    components:{
        JCDialog,
        AddBtn
    },
    data(){
        return{
            title:'标题',
            // con:'这里是内容区',
            sendVal: false,
        }
    },
    // computed:{
    //     getCon(){
    //         var str = '<h1></h1>'
    //         return str;
    //     }
    // },
    methods:{
        openMask(index){
            this.sendVal = true;
        },
        clickCancel(){
            console.log('点击了取消');
        },
        clickDanger(){
            console.log('这里是danger回调')
        },
        clickConfirm(){
            console.log('点击了confirm');
        }
    }
}
</script>