<template>
    <div>
        <JCHeader></JCHeader>
        <JCMain></JCMain>  
    </div>
</template>
<script>

import JCHeader from '@/components/layout/header'
import JCMain from '@/components/layout/main'
import api from '@/utils/api/';
export default {
    components: {
        JCMain,
        JCHeader
    },
    data(){
        return {
            githubObject: {}
        }
    },
    mounted(){
        this.getData()
    },
    methods: {
        async getData(){
            const result = await api.getDesigner('https://api.github.com');
            this.githubObject = result.data;
        }
    }
}
</script>

<style lang="scss">
@import './components/sass/layout.scss';
</style>
