<template>
    <div class="ide-mainwrap">
        <draggable class="ide-mainwrapcon"  :options="dragOptions" :element="'div'" :list="layoutData">
            <transition-group tag="div" class="ide-iphone6 ide-scrollbar">
                <component v-bind:is="widget.view" v-for="(widget , index) in layoutData" :key="'component' + index"></component>
            </transition-group>
        </draggable>
    </div>
</template>
<script>

export default {
    name: 'MainWrap',
    data(){
        return {
            layoutData: [],
            dragOptions: {
                sort: true,
                group: {
                    name: "mobile-widget",
                    pull: false,
                    put: true
                },
                disabled: false,
                ghostClass: "ghost"
            }
        }
    }
}
</script>
<style>
.ide-iphone6 .drag-menu-item{
    list-style: none;
    border: 1px solid #f90;
    height: 50px;
    line-height: 50px;
}
.ide-iphone6 .drag-menu-item i{ 
    display: none;
}
</style>
