//http插件
import axios from 'axios';

//请求目标地址
const Ajax = window.cefQuery ? (function () {
    return {
        get(...params) {
            return new Promise((resolve, reject) => {
                window.cefQuery({
                    request: params[0],
                    onSuccess: function onSuccess(response) {
                        resolve(response);
                    },
                    onFailure: function onFailure(response) {
                        reject(response);
                    }
                });
            });
        },
        post(...params) {
            return new Promise((resolve, reject) => {
                window.cefQuery({
                    request: params[0] + params[1].formJson,
                    onSuccess: function onSuccess(response) {
                        resolve(response);
                    },
                    onFailure: function onFailure(response) {
                        reject(response);
                    }
                });
            });
        }
    }
})() : axios.create({
    baseURL: process.env.BASE_URL,
    timeout: 5000,
    // headers: {
    // 	'Content-Type': 'application/x-www-form-urlencoded',
    // }
});

/**
 * @function api 统一请求接口封装类， web端和ide端通用
 * @example
 *  import api from '@/utils/api/'
 *   
 *  api.getDesigner().then((response)=>{
 *      console.log(response);
 *  }).catch((err)=>{
 *      throw new Error('请求报错');
 *  });
 * 
 */
export default {
	/**
	 * 获取表单全部数据
	 * 
 	*/
    getDesigner() {
        return Ajax.get('https://api.github.com');
    },
	/**
	 * 测试POST请求
	 * 
	 * @param {any} json 
	 */
    save(params) {
        return Ajax.post('save.action', {
            formJson: params
        })
    },
	/**
	 * 所有接口完成回调
	 * @example
	 * api.getAll(api.getDesigner(),api.getSqlTable(),api.getUsers())
	 * .then((res)=>{
	 *    res[0]		getDesigner的响应信息
	 *    res[1]		getSqlTable的响应信息
	 *    res[2]		getUsers   的响应信息
	 * })
	 * 
	 */
    getAll(...params) {
        return axios.all(params);
    }
}