<template>
    <div class="left-assembly">
        <div class="left-hd d-flex">
            <i class="ico ico-leftassembly"></i>组件库
        </div>
        <ul class="Left-bd ide-scrollbar">
            <!--表单组件-->
            <li class="Left-bd-li ghost" v-for="(group ,key) in widgets" :key="'leftType'+key">
                <h2 class="Left-bd-hd" :class="key === selectedGroup ? 'selected' :''" @click.stop="expand(key)">{{key}}</h2>
                <draggable :options="dragOptions" :list="group" class="sub-list left-ul d-flex" :element="'ul'" v-if="key === selectedGroup && group && group.length">
                    <li v-for="(item ,index) in group" :key="'widget_itme_'+index" class="drag-menu-item">
                        <i :class="'fa '+ item.icon"></i><p>{{item.name}}</p>
                    </li>
                </draggable>
            </li>
            <!--表单组件end-->
        </ul>
    </div>
</template>
<script>

export default {
    name: 'LeftsidebarAssembly',
    props: {
        widgets: {
            type: Object,
            default(){
                return {}
            }
        }
    },
    data(){
        return {
            selectedGroup: '基础组件',
            dragOptions: {
                sort: false,
                group: {
                    name: "mobile-widget",
                    pull: "clone",
                    put: false
                },
                disabled: false,
                ghostClass: "ghost"
            }
        }
    },
    methods: {
        /**
         * @function
         * @param group 当前分组对象
         */
        expand(group){
            this.selectedGroup = group;
        }
    }
}
</script>