<template>
  <!--单选项带条件-->
    <div class="form-group d-flex">
        <label class="control-label">是否验证</label>
        <div class="form-group-w">
            <label class="form-group-label mb8"
             v-for="(item,index) in radioData"
             @click="select(item)"
             :class="{'selected': item.isChecked}" 
             :key="index"
             >
                <span class="ide-radio"></span>
                <span class="ide-radio-label">{{item.name}}</span> 
            </label>
            <div v-show="flag" class="input-group"><textarea required="required" rows="3" class="form-control" placeholder="验证规则:[请用 , 分隔] "></textarea></div>
        </div>
    </div>
</template>
<script>
  export default {
    name: 'FormRadio2',
    data() {
      return {
        flag: true,
        radioData: [
          {
            name: '是',
            isChecked: true
          },
          {
            name: '否',
            isChecked: false
          }
          // {
          //   name: '其他参数',
          //   isChecked: false
          // }
        ]
      }
    },
    methods: {
      select(item) {
        this.radioData.forEach((item) => {
          item.isChecked = false
        })
        this.$set(item, 'isChecked', true)
        if (item.name === '是') {
          this.flag = true
        } else {
          this.flag = false
        }
      }
    }
  }
</script>