/* eslint-disable */
/**
 * 配置属性实体类
 */
class Property {
    constructor(args) {
		let _this = this;
		Object.keys(args).forEach((property) =>{
			if(!_this[property]){
				_this[property] = _this.copyObject(args[property]);
			}
		});
    }
    
    /**
	 * 复制对象,避免指针相同,多个组件属性级联问题
	 * 
	 * @param 	{any} 		obj  需要复制的对象
	 * @memberof Property
	 */
	copyObject(obj){
		let item ,_this = this;
		switch(typeof obj){
			case 'string':
				item = String(obj);
			break;
			case 'boolean':
				item = Boolean(obj);
			break;
			case 'object':
				item = _this.deepCopy(obj)
			break;
			default:
			    item = item;
		}
		return item
	}
	/**
	 * 数组or对象深层复制
	 * 代码中我用instanceof来判断类型，一定要把o instanceof Array 放在 o instanceof Object上面，
	 * 因为如果是数组的话，instanceof Object也返回true。
	 * 
	 * @param {any} o 
	 * @returns 
	 * @memberof Property
	 */
	deepCopy(o) {
		let _this = this;
		if (o instanceof Array) {
			let n = [];
			for (let i = 0; i < o.length; ++i) {
				n[i] = _this.deepCopy(o[i]);
			}
			return n;
 
		} else if (o instanceof Object) {
			let n = {}
			for (let i in o) {
				n[i] = _this.deepCopy(o[i]);
			}
			return n;
		} else {
			return o;
		}
	}
}


export default Property;