<template>
    <!--弹出框-->
    <div class="dialog" v-show="showMask">
        <div class="dialog-content">
            <div class="dialog-header">
                <h4 class="dialog-tit">
					{{title}}
				</h4>
                <div class="close-btn" @click="closeMask"><i class="jcm-guanbi"></i></div>
            </div>
            <div class="dialog-body">
                <slot name='D-option'></slot>
            </div>
            <div class="dialog-footer">
                <button v-if="type != 'danger'" class="btn default-btn" @click="closeBtn">
                    {{cancelText}}
                </button>
                <button v-if="type == 'danger'" class="btn danger-btn" @click="dangerBtn">
                    {{dangerText}}
                </button>
                <button v-if="type == 'confirm'" class="btn confirm-btn" @click="confirmBtn">
                    {{confirmText}}
                </button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "JCDialog",
    props: {
        value: {},
        // 类型包括 defalut 默认， danger 危险， confirm 确认，
        type:{
            type: String,
            default: 'default'
        },
        content: {
            type: String,
            default: ''
        },
        title: {
            type: String,
            default: ''
        },
        cancelText: {
            type: String,
            default: '取消'
        },
        dangerText: {
            type: String,
            default: '删除'
        },
        confirmText: {
            type: String,
            default: '确定'
        },
    },
    data(){
        return{
            showMask: false,
        }
    },
    methods:{
        closeMask(){
            this.showMask = false;
        },
        closeBtn(){
            this.$emit('cancel');
            this.closeMask();
        },
        dangerBtn(){
            this.$emit('danger');
            this.closeMask();
        },
        confirmBtn(){
            this.$emit('confirm');
            this.closeMask();
        }
    },
    mounted(){
        this.showMask = this.value;
    },
    watch:{
        value(newVal, oldVal){
            this.showMask = newVal;
        },
        showMask(val) {
            this.$emit('input', val);
        }
    },
}
</script>
<style lang="scss">
.dialog{
    position:fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.6);
    z-index: 9999;
    .dialog-content{
        width:30%;
        top: 50%;
        left: 50%;
        position: relative;
        background-color: #fff;
        border-radius:4px;
        outline: 0;
        box-shadow: 0 1px 3px rgba(0,0,0,.3);
        transform: translate(-50%, -50%);
        .dialog-header{
            padding:15px 20px;
            border-bottom:1px solid#ebebeb;
            .dialog-tit{
                line-height:24px;
                font-size: 18px;
                color: #333;
                font-weight:normal;
            }
            .close-btn{
                position: absolute;
                top: 16px;
                right: 16px;
                line-height:20px;
                text-align: center;
                font-size: 18px;
                cursor: pointer;
                &:hover{
                    font-weight: 600;
                }
            }
        }
        .dialog-body{
            position: relative;
            padding:30px 20px;
        }
        .inp{
            margin: 10px 0 0 20px;
            width: 200px;
            height: 40px;
            padding-left: 4px;
            border-radius: 4px;
            border: none;
            background: #efefef;
            outline: none;
            &:focus{
                border: 1px solid #509EE3;
            }
        }
        .dialog-footer{
            padding:15px 20px;
            text-align: right;
            .default-btn{
                margin-right: 10px;
                border-color: #dcdfe6;
                &:hover{
                    background-color: #f3f7ff;
                    border-color: #c6d8fa;
                }
                &:active{
                    border-color: #88abee;
                }
            }
            .danger-btn{
                color: #fff;
                background-color: red;
                border-color: red;
                &:hover{
                    background-color: red;
                    border-color: red;
                }
            }
            .confirm-btn{
                color: #fff;
                background-color: #6997ed;
                border-color: #6997ed;
                &:hover{
                    background-color: #8db4fd;
                    border-color: #8db4fd;
                }
            }
        }
    }
}
</style>