<template>
    <!--多选项-->
    <div class="form-group">
        <div class="form-tit mb8 d-flex">
            <span class="form-tit-line"></span>
            <h2>选择项</h2>
        </div>
        <!--多选内容-->
        <div class="form-optbox pin" 
            v-for="(item,index) in radioData"
            :key="index"
            @click="showToggle(item)"
            :class="{'toggle': item.isChecked}" 
            >
            <!--添加+删除按钮-->
            <div class="actions">
                <button type="button" class="btn btn-add mr5"
                @click="moveKey"
                >
                    <i class="fa jcm-trash6"></i>
                </button>
                <button type="button" class="btn btn-del"  
                @click="clickKey">
                    <i class="fa jcm-add6"></i>
                </button>
            </div>
            <div class="form-group d-flex align-items-center">
                <label class="control-label">显示值</label>
                <div class="form-group-w">
                    <div class="input-group">
                        <input type="text" v-model="item.value" placeholder="请输入显示值" class="form-control">
                    </div>
                </div>
            </div>
            <div class="form-group d-flex align-items-center">
                <label class="control-label">value值</label>
                <div class="form-group-w">
                    <div class="input-group">
                        <input type="text" v-model="item.key"  placeholder="请输入value值" class="form-control">
                    </div>
                </div>
            </div>
            <!--Checkbox-->
            <FormCheckbox></FormCheckbox>
            <!--Checkbox end-->
        </div>
        <!--多选内容end-->
    </div>
    <!--多选项end-->
</template>
<script>
import FormCheckbox from './FormCheckbox'
export default {
    name: 'FormCheckbox2',
    components:{
        FormCheckbox,
    },
    data(){
        return {
            radioData: [
                {
                    value: '是',
                    key:222,
                    isChecked: false
                }
            ]
        }
    },
    methods:{
        //添加html
        clickKey(){ 
            this.radioData.push( {
                value: '否',
                key:111,
                isChecked: true
            })
        },
        //删除html
        moveKey(index){
            //判断当前删除的html大于1时，就不在删除添加的HTML
            if(this.radioData.length > 1){
                this.radioData.splice(index,1);
            }
        },
        //点击添加样式
        showToggle(item){
            this.radioData.forEach((item) => {
            item.isChecked = true
            })
            this.$set(item, 'isChecked', false)
        }  
    }
}
</script>